.search-wrap{height:100%}.search-wrap .no-result{position:absolute;top:50%;left:50%;transform:translate(-50%,-50%);color:#969798}.search-wrap .reach-max-tip{position:fixed;top:calc(50% - 190px);left:50%;transform:translate(-50%,-50%);padding:0 12px;box-shadow:0 2px 2px 2px rgba(0,0,0,.1);border-radius:2px;color:#fe4066;background:#fff}.search-wrap .reach-max-tip i{top:1px;margin-right:8px}