.dsm-c-pagination-backward,.dsm-c-pagination-forward{display:inline-block;min-width:32px;height:32px;line-height:32px;text-align:center;vertical-align:middle;list-style:none;border-radius:2px;cursor:pointer;margin-right:8px;position:relative}.dsm-c-pagination-backward svg,.dsm-c-pagination-forward svg{display:inline-block;width:12px;height:12px;opacity:0;transition:all .2s;vertical-align:-1.25px;margin-top:10px}.dsm-c-pagination-backward .dsm-c-pagination-ellipsis,.dsm-c-pagination-forward .dsm-c-pagination-ellipsis{position:absolute;left:0;right:0;top:0;bottom:0;display:block;color:rgba(0,0,0,.25);letter-spacing:2px;opacity:1;transition:all .2s;text-align:center}.dsm-c-pagination-backward:hover .dsm-c-pagination-ellipsis,.dsm-c-pagination-forward:hover .dsm-c-pagination-ellipsis{opacity:0}.dsm-c-pagination-backward:hover svg,.dsm-c-pagination-forward:hover svg{opacity:1}.dsm-c-pagination.red .dsm-c-pagination-backward,.dsm-c-pagination.red .dsm-c-pagination-forward{color:#fe4066}.dsm-c-pagination.blue .dsm-c-pagination-backward,.dsm-c-pagination.blue .dsm-c-pagination-forward{color:#009dff}