.dsm-c-pagination-size-changer{display:inline-block;margin-right:8px}.dsm-c-pagination-size-changer .dsm-c-select{height:32px;transition:all .3s cubic-bezier(.645,.045,.355,1);border-radius:2px}.dsm-c-pagination-size-changer .dsm-c-select-title{font-size:14px;line-height:30px}.dsm-c-pagination-size-changer .dsm-c-select.full-border .dsm-c-select-title{padding-left:12px}.dsm-c-pagination-size-changer .dsm-c-select .dsm-c-icon.icon-_tag_downarrow{color:#dcddde}.dsm-c-pagination-size-changer .dsm-c-select:hover{border-color:#fe4066}.dsm-c-pagination-size-changer .dsm-c-select:hover .dsm-c-tooltip>.dsm-c-select-title{color:#2e2f30}.dsm-c-pagination-size-changer .dsm-c-select .direction-up .dsm-c-drop{min-width:95px;bottom:calc(100% + 6px)}.dsm-c-pagination-size-changer .dsm-c-select .direction-up .dsm-c-drop .menu-item{padding:0 16px}.dsm-c-pagination-size-changer.red .dsm-c-select:hover{border-color:#fe4066}.dsm-c-pagination-size-changer.red:focus{outline:none}.dsm-c-pagination-size-changer.red:focus .dsm-c-select{border-color:#fe4066;box-shadow:0 0 0 2px rgba(254,64,102,.2)}.dsm-c-pagination-size-changer.blue .dsm-c-select:hover{border-color:#009dff}.dsm-c-pagination-size-changer.blue:focus{outline:none}.dsm-c-pagination-size-changer.blue:focus .dsm-c-select{border-color:#009dff;box-shadow:0 0 0 2px rgba(24,144,255,.2)}