.dsm-c-loading-points{position:fixed;top:0;left:0;right:0;bottom:0;z-index:3001;display:flex;align-items:center;justify-content:center}.dsm-c-loading-points.mask{background-color:hsla(0,0%,100%,.3)}.dsm-c-loading-points.shadow .loading-container{background:#fff;box-shadow:0 12px 30px 0 rgba(0,0,0,.16)}.dsm-c-loading-points .loading-container{width:100px;height:100px;border-radius:8px;display:flex;align-items:center;justify-content:center}.dsm-c-loading-points .loading-container img{pointer-events:none;width:48px;height:48px}