.dsm-c-top-message-tip{position:fixed;left:50%;top:32px;transform:translateX(-50%);min-width:480px;max-width:80%;z-index:3001}.dsm-c-top-message-tip .text-container{display:flex;align-content:center;justify-content:space-between;padding:14px 32px;border-radius:6px;background:#fff;box-shadow:0 5px 30px 0 rgba(0,0,0,.15)}.dsm-c-top-message-tip .text-container .text-info{line-height:28px}.dsm-c-top-message-tip .text-container .text-info .target-str{font-weight:600}.dsm-c-top-message-tip .link-b{display:block;min-width:48px;padding:0 10px;border-radius:4px;line-height:28px;transition:all .2s}.dsm-c-top-message-tip .link-b:hover{background:#ebedee;text-decoration:none}.dsm-c-top-message-tip .close-btn{position:absolute;right:7px;top:6px}