.dsm-c-rp-badge{position:relative;display:inline-block}.dsm-c-rp-badge .dsm-c-rp-badge-item{position:absolute;overflow:hidden;right:0;top:0;background-color:#fe4066;-webkit-box-sizing:border-box;box-sizing:border-box;z-index:1}.dsm-c-rp-badge .dsm-c-rp-badge-item.right{top:50%;-webkit-transform:translate(calc(100% + 4px),-55%);transform:translate(calc(100% + 4px),-55%)}.dsm-c-rp-badge .dsm-c-rp-badge-item.right-top{-webkit-transform:translate(6px,-4px);transform:translate(6px,-4px)}.dsm-c-rp-badge .dsm-c-rp-badge-item.count{min-width:16px;height:16px;border-radius:16px;padding:0 4px 0 5px;font-size:12px;color:#fff;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.dsm-c-rp-badge .dsm-c-rp-badge-item.dot{border-radius:50%}.dsm-c-rp-badge .dsm-c-rp-badge-item.dot.default{width:8px;height:8px}.dsm-c-rp-badge .dsm-c-rp-badge-item.dot.mini{width:5px;height:5px}